.guide-button a {
  
  text-decoration: none;
  
  

} 


a.guide-in{
    display: inline-block;
    font-size: 3rem;
    color: mediumvioletred;
    text-decoration: none;
    animation: fadeIn ease-out 3s;
    -webkit-animation: fadeIn ease-out 3s;
    -moz-animation: fadeIn ease-out 3s;
    -o-animation: fadeIn ease-out 3s;
    -ms-animation: fadeIn ease-out 3s;
  }
  
  @keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-moz-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-webkit-keyframes fadeIn {
    0% {opacity:0;}
    100%{opacity:1;}
  }
  
  @-o-keyframes fadeIn {
    0% {opacity:0;}
    100% {opacity:1;}
  }
  
  @-ms-keyframes fadeIn{
    0% {opacity:0;}
    100% {opacity:1;}
  }

  a.guide-out{
    display: inline-block;
    font-size: 1rem;
    color: cornsilk;
    text-decoration: none;
    animation: fadeOut linear 3;
    -webkit-animation: fadeOut linear 3s;
    -moz-animation: fadeOut linear 3s;
    -o-animation: fadeOut linear 3s;
    -ms-animation: fadeOut linear 3s;
  }
  
  @keyframes fadeOut {
    0% {opacity:1;}
    100% {opacity:0;}
  }
  
  @-moz-keyframes fadeOut {
    0% {opacity:1;}
    100% {opacity:0;}
  }
  
  @-webkit-keyframes fadeOut {
    0% {opacity:1;}
    100% {opacity:0;}
  }
  
  @-o-keyframes fadeOut {
    0% {opacity:1;}
    100% {opacity:0;}
  }
  
  @-ms-keyframes fadeOut{
    0% {opacity:1;}
    100% {opacity:0;}
  }

  @media only screen and (max-width: 600px) {
    a.guide-in{
      font-size: 1.5em;
    }
  }
